'use client';

import { useState } from 'react';
import Image from 'next/image';
import { useParams } from 'next/navigation';

const categoryData = {
  equipment: {
    name: 'Equipment',
    description: 'Professional-grade gym equipment for your home or commercial gym.',
    products: [
      {
        id: 1,
        name: 'Premium Dumbbell Set',
        price: 299.99,
        image: '/images/dumbbell-set.jpg',
        description: 'Professional grade dumbbell set with rack, perfect for home gyms.',
      },
      // Add more equipment products
    ],
  },
  accessories: {
    name: 'Accessories',
    description: 'Essential accessories to enhance your workout experience.',
    products: [
      {
        id: 2,
        name: 'Resistance Bands Pack',
        price: 49.99,
        image: '/images/resistance-bands.jpg',
        description: 'Set of 5 resistance bands with different tension levels.',
      },
      // Add more accessories products
    ],
  },
  supplements: {
    name: 'Supplements',
    description: 'Premium supplements for optimal performance and recovery.',
    products: [
      {
        id: 3,
        name: 'Whey Protein Powder',
        price: 59.99,
        image: '/images/protein-powder.jpg',
        description: 'Premium whey protein powder for muscle recovery and growth.',
      },
      // Add more supplements products
    ],
  },
  clothing: {
    name: 'Clothing',
    description: 'High-performance workout apparel for maximum comfort.',
    products: [
      {
        id: 4,
        name: 'Performance T-Shirt',
        price: 29.99,
        image: '/images/tshirt.jpg',
        description: 'Moisture-wicking performance t-shirt for intense workouts.',
      },
      // Add more clothing products
    ],
  },
};

const sortOptions = ['Price: Low to High', 'Price: High to Low', 'Name: A to Z', 'Name: Z to A'];

export default function CategoryPage() {
  const params = useParams();
  const category = params.category;
  const [sortBy, setSortBy] = useState(sortOptions[0]);
  const [searchQuery, setSearchQuery] = useState('');

  if (!categoryData[category]) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-16 text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-4">Category Not Found</h1>
        <p className="text-gray-600">The category you're looking for doesn't exist.</p>
      </div>
    );
  }

  const { name, description, products } = categoryData[category];

  // Filter products based on search query
  const filteredProducts = products.filter((product) =>
    product.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    product.description.toLowerCase().includes(searchQuery.toLowerCase())
  );

  // Sort products based on selected option
  const sortedProducts = [...filteredProducts].sort((a, b) => {
    switch (sortBy) {
      case 'Price: Low to High':
        return a.price - b.price;
      case 'Price: High to Low':
        return b.price - a.price;
      case 'Name: A to Z':
        return a.name.localeCompare(b.name);
      case 'Name: Z to A':
        return b.name.localeCompare(a.name);
      default:
        return 0;
    }
  });

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Category Header */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-gray-900 mb-4">{name}</h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          {description}
        </p>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-8">
        <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto">
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="border rounded-md px-4 py-2"
          >
            {sortOptions.map((option) => (
              <option key={option} value={option}>
                {option}
              </option>
            ))}
          </select>
          <input
            type="text"
            placeholder="Search products..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="border rounded-md px-4 py-2 w-full sm:w-auto"
          />
        </div>
      </div>

      {/* Products Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8">
        {sortedProducts.map((product) => (
          <div
            key={product.id}
            className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow duration-300"
          >
            <div className="relative h-48">
              <Image
                src={product.image}
                alt={product.name}
                fill
                className="object-cover"
              />
            </div>
            <div className="p-4">
              <h3 className="text-lg font-semibold text-gray-900">{product.name}</h3>
              <p className="text-gray-600 mt-2 text-sm line-clamp-2">
                {product.description}
              </p>
              <p className="text-gray-900 font-semibold mt-2">${product.price}</p>
              <button className="w-full mt-4 bg-gray-900 text-white py-2 rounded-md hover:bg-gray-800">
                Add to Cart
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Empty State */}
      {sortedProducts.length === 0 && (
        <div className="text-center py-12">
          <h3 className="text-lg font-medium text-gray-900 mb-2">No products found</h3>
          <p className="text-gray-600">
            Try adjusting your search criteria
          </p>
        </div>
      )}
    </div>
  );
} 